import { useState, useEffect, useContext, useCallback } from "react";
import { getBedExitDurationStatistic } from "@/api/deviceReports";

import BarChart from "@/Components/GraphAndChart/barChart";
import Template from "@/Components/GraphAndChartTemp/Template";
import { getTemplateData } from "@/Pages/Elderlies/Components/Utiles/utiles";
import { CustomContext } from "@/Context/UseCustomContext";
import dayjs from "dayjs";
export default function BedExitDurationStatistic() {
  const { title, color, icon, dataAnalysis, summaryProps } =
    getTemplateData("Bed Exit Duration");

  const context = useContext(CustomContext);
  const {
    chartToDate = "",
    chartFromDate = "",
    elderlyDetails,
  } = context || {};
  const [toDate, setToDate] = useState(
    dayjs().subtract(1, "day").format("YYYY-MM-DD")
  );
  const [fromDate, setFromDate] = useState(
    dayjs().subtract(31, "day").format("YYYY-MM-DD")
  );
  const [bedExitDurationStatistic, setBedExitDurationStatistic] = useState([]);
  const getBedExitDurationStatisticData = useCallback(() => {
    getBedExitDurationStatistic({
      uids: elderlyDetails?.bedRoomIds,
      elderly_id: elderlyDetails._id,

      to_date: fromDate,
      from_date: toDate,
      status: 3,
    })
      .then((res) => {
        setBedExitDurationStatistic(filteredData(res.data));
      })
      .catch((err) => {
        console.log(err);
      });
  }, [toDate, elderlyDetails]);
  useEffect(() => {
    getBedExitDurationStatisticData();
  }, [getBedExitDurationStatisticData]);
  useEffect(() => {
    if (chartToDate && chartFromDate) {
      setToDate(chartToDate);
      setFromDate(chartFromDate);
    }
  }, [chartToDate]);

  function filteredData(data) {
    if (!Array.isArray(data)) {
      console.error("Invalid data format: Expected an array.");
      return [];
    }

    const newData = data.map((item) => {
      // Calculate the total duration from sleep_index_common_list
      const totalBedExitDuration =
        item.sleep_index_common_list?.reduce((total, listItem) => {
          const value = Number(listItem?.value) || 0; // Convert value to a number
          return total + value;
        }, 0) || "0"; // Default to 0 if no valid values are present

      return {
        date: item?.date || null, // Default to null if date is missing
        bed_exit_duration: totalBedExitDuration, // Total duration as a number
      };
    });

    return newData;
  }
  return (
    <Template
      title={title}
      color={color}
      icon={icon}
      dataAnalysis={dataAnalysis}
      summaryProps={summaryProps}
    >
      <BarChart
        data={bedExitDurationStatistic}
        color={color}
        dataType="duration"
        fromDate={fromDate}
        toDate={toDate}
      />
    </Template>
  );
}
